@mixin breakpoint($point) {
    @media (max-width: $point) {
        @content;
    }
}

.Game {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 40px;
    margin-top: 70px;
    @include breakpoint(572px) {
        margin-top: 20px;
        gap: 20px;
    }

    .user-container {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        border: 4px solid #3d3f3c;
        padding: 25px;
        border-radius: 20px;
        animation: tilt-shaking 3s ease-in 1se;

        @include breakpoint(435px) {
            width: 100%;
            margin: 0 20px 0 20px;
        }

        &.animation {
            animation: 0.4s ease-in tilt-shaking forwards;
        }

        @keyframes tilt-shaking {
            0% {
                transform: rotate(0deg);
            }
            25% {
                transform: rotate(5deg);
            }
            50% {
                transform: rotate(0eg);
            }
            75% {
                transform: rotate(-5deg);
            }
            100% {
                transform: rotate(0deg);
            }
        }
    }

    .wins {
        border: 4px solid #4e9b3b;
        .picture {
            border: 4px solid #4e9b3b;
        }
    }
    .loses {
        border: 4px solid #b73619;
        .picture {
            border: 4px solid #b73619;
        }
    }
    .equally {
        border: 4px solid #3d3f3c;
        .picture {
            border: 4px solid #3d3f3c;
        }
    }
}

.User {
    display: flex;
    flex-direction: column;
    align-items: center;
    .picture {
        width: 90px;
        height: 90px;
        border-radius: 50%;
        object-fit: cover;
    }
    .name {
        margin: 5px 0;
        color: #3d3f3c;
    }
    .candys {
        margin: 10px 0;
        color: #3d3f3c;
    }
}

.CountNavigation {
    display: flex;
    .button {
        width: 50px;
        height: 50px;
        margin: 10px;
        border: 2px solid #3d3f3c;
        border-radius: 10px;
        background: white;
        font-size: 25px;
        color: #3d3f3c;
        cursor: pointer;
    }
}

.ToggleVersions {
    display: flex;
    justify-content: center;
    .button {
        padding: 15px;
        border-radius: 15px;
        margin: 10px;
        background: no-repeat;
    }
}
